@import "../../styles/variables";
@import "../../styles/mixins";

#nav-btn{
  @include normalBoldFont;
  @include buttonCorrect;
  display: flex;
  font-size: 1rem;
  border-bottom: 0 !important;
  padding: 5% 20px;
};

#extension-btn{
  @include centralAlignFlex;
  @include normalBoldFont;
  padding: 1% 3%;
  display: block;
  width: auto;
  font-size: 1.125rem;
  line-height: 1.375rem;
};

#nav-btn, #extension-btn{
  @include buttonCorrect;
  @include normalBoldFont;
  background-color: #FF6A70;
  border-radius: 110px;
  letter-spacing: 1px;
  color: $color !important;
  transition: all 0.3s linear;
};

#list-for-start-btn{
  @include centralFlexPosition;
  width: 8vw;
  height: 100%;
};

#nav-btn:hover, #extension-btn:hover{
  background-color: #ffb43f;
};

@media(max-width: "599px") {

  #extension-btn{
    padding: 4% 10% !important;
  };

};